.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: black;
    height: 100vh;
    position: relative;
}

.errorNumber {
    font-family: montserrat, sans-serif;
    font-size: 224px;
    font-weight: 900;
    text-shadow: -1px -1px 0 #8400ff, 1px 1px 0 #ff005a;
}

.errorText {
    position: absolute;
    top: 52%;
    font-family: montserrat, sans-serif;
    font-size: 55px;
    font-weight: 700;
    letter-spacing: 10px;
    color: #fff;
    text-shadow: 0 2px 0 #8400ff;
}

.symbol {
    position: absolute;
    color: #ffffff;
    font-size: 20px;
    top: 63%;
    left: 56%;
}

.homePageLink {
    color: #ff005a;
    font-weight: bold;
    letter-spacing: 3px;
    border: 2px solid;
    padding: 10px 30px;
    transition: color 300ms ease;
}

.homePageLink:hover {
    color: #8400ff;
}